.scrollbar::-webkit-scrollbar {
  display: none;
}

.scrollbar {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.pill-hover {
  cursor: pointer;
  border-radius: 500px;
}

.pill-hover:hover {
  /* background-color: #f1f1f1; */
  color: #1da1f2;
}

.mr-3 {
  margin-right: 35px;
}

.main {
  background-color: #141d26;
}

.post:hover {
  background-color: #1c2c3b;
  cursor: pointer;
}

.date-clr {
  color: #5f6874;
}

.border-lr {
  border-left: solid 1px;
  border-right: solid 1px;
  border-color: #243447;
}

.border-btm {
  border-bottom: solid 1px;
  border-color: #243447;
}

.pill-button {
  background-color: #1da1f2;
  cursor: pointer;
  border-radius: 50px;
}

.half-pill-button {
  background-color: #1da1f2;
  cursor: pointer;
  border-radius: 50px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.pill-button:hover {
  background-color: #3490c9;
}

.border-rounded {
  border: solid 2px;
  border-radius: 50%;
  border-color: #2c3847;
}

textarea {
  background: transparent;
  outline: none;
}

textarea:focus {
  color: white;
}

textarea::placeholder {
  color: #657786;
}

.color-primary {
  color: #1da1f2;
}

.link-nav-active {
  color: #1da1f2;
}

.social{
  color: #5f6874;
}

.social:hover {
  opacity: 0.7;
  cursor: pointer;
}
